<template>
    <el-dialog title="应用程序详情" align-center width="800px">

        <div class="info-parent">
            <MyIcon class="info-icon" :src="item.icon" :size="199" :title="item.name" />

            <el-form class="info-form" label-width="128px">
                <el-form-item label="标题"> {{ item.title }} </el-form-item>
                <el-form-item label="名称"> {{ item.name }} </el-form-item>

                <el-form-item label="ID / UUID"> {{ item.id }} / {{ item.uuid }} </el-form-item>

                <el-form-item label="别名"> {{ item.aliases }} </el-form-item>
                <el-form-item label="命名空间"> {{ item.namespace }} </el-form-item>
                <el-form-item label="优先级"> {{ item.open_with_priority }} </el-form-item>
                <el-form-item label="路径"> {{ item.path }} </el-form-item>
                <el-form-item label="校验和"> {{ item.sha256sum }} </el-form-item>
                <el-form-item label="大小"> {{ item.size }} </el-form-item>
                <el-form-item label="状态"> {{ item.state }} </el-form-item>
                <el-form-item label="URN"> {{ item.urn }} </el-form-item>
                <el-form-item label="标签"> {{ item.tags }} </el-form-item>
                <el-form-item label="系统/架构"> {{ item.os }} / {{ item.arch }} </el-form-item>

                <el-form-item label="描述"> {{ item.description }} </el-form-item>

                <el-form-item label="创建时间"> {{ formatTime(item.created_at) }} </el-form-item>
                <el-form-item label="修改时间"> {{ formatTime(item.updated_at) }} </el-form-item>
            </el-form>
        </div>

        <div>
            <ElButton type="default" @click="close">关闭</ElButton>
        </div>

    </el-dialog>
</template>

<script>
import { ElButton } from 'element-plus';
import MyIcon from '@/components/utils/icon.vue'
import TimeJS from '@/js/time.js'

export default {
    name: "settings-local-repository-item-detail-dialog",

    components: { ElButton, MyIcon },

    computed: {
        item() {
            return this.target;
        }
    },

    methods: {
        close() {
            this.$emit('update:modelValue', false)
        },

        formatTime(ms) {
            return TimeJS.StringifyTimestamp(ms, 'datetime')
        },
    },

    props: {
        target: Object
    }
}
</script>

<style>
.info-parent {
    display: flex;
    flex-direction: row;
}

.info-icon {
    flex-grow: 0;
}

.info-form {
    flex-grow: 1;
}
</style>
